import { TitleCasePipe } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { EmpexpenseserviceService } from 'src/app/core/services/empexpenseservice.service';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-edit-expencemployee',
  templateUrl: './edit-expencemployee.component.html',
  styleUrls: ['./edit-expencemployee.component.scss']
})
export class EditExpencemployeeComponent implements OnInit {
  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public editexpenseForm: any;
  productPicUrl: any;
  userId: any;
  expenseId: any;
  public submitted: boolean = false;
  public today: Date;
  constructor(public dialogRef: MatDialogRef<EditExpencemployeeComponent>, private cdr: ChangeDetectorRef, private formBuilder: FormBuilder, public router: Router, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, @Inject(MAT_DIALOG_DATA) public UserData: any, private productService: ProductService,
    public expenseservice: EmpexpenseserviceService) {
      this.today = new Date();
     }

  ngOnInit(): void {
    this.userId = this.UserData.userId;
    this.expenseId = this.UserData.expenseId;

    this.createEductionForm();
    this.getExpenseById();

  }

  get f() {
    return this.editexpenseForm.controls;
  }
  createEductionForm() {
    this.editexpenseForm = this.formBuilder.group({
      name: ['',[Validators.required]],
      startDate: ['',[Validators.required]],
      type: ['',[Validators.required]],
      amount: [null,[Validators.required]],
      description: ['',[Validators.required]],
      //uploadDocument: [""],


    });
  }

  getExpenseById() {
    this.expenseservice.GetExpenseByExpenseId({}, this.expenseId).then((res: any) => {
      console.log("RES expense BY ID", res);

      this.productPicUrl = res.data.receiptImage;
      this.editexpenseForm.patchValue({

        name: res.data.name,
        startDate: res.data.date ? (moment(res.data.date).format("DD-MMM-YYYY")) : (moment(res.data.date).format("DD-MMM-YYYY")),
        type: res.data.category,
        amount: res.data.enterAmount,
        description: res.data.description,


      })

    })

  }

  UpdateEducation(){
    this.submitted = true;
    if (this.editexpenseForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields."
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else{
      let Body={
        "name": this.editexpenseForm.controls.name.value,
        "date": this.editexpenseForm.controls.startDate.value,
        "category":this.editexpenseForm.controls.type.value,
        "enterAmount": this.editexpenseForm.controls.amount.value,
        "description": this.editexpenseForm.controls.description.value,
        "receiptImage": this.productPicUrl,
        "employeeId": this.currentEmployee.id
         
      }
      this.expenseservice.ExpenseUpdateByEmployeeId(Body,this.expenseId).then((res:any)=>{
        // this.toastService.openSnackBar(" Expense Updated Successfully");
        this.toastService.toastMsg({
          title: "Success",
          content: "Expense Updated Successfully!!!",
        });
        this.expenseservice.notifyExpenseAdded();
        this.dialogRef.close();
      },(err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");      
        } 
       })

    }

    
  }
  openDatepicker() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.editexpenseForm.get('startDate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.editexpenseForm.get('startDate').setValue(new Date());
    }
  }
  numberDecimalOnly(event): boolean {
    var charCode = (event.which) ? event.which : event.keyCode;
    if (charCode != 46 && charCode > 31
      && (charCode < 48 || charCode > 57))
      return false;

    return true;
  }

  documentChange(e) {
    let formdata = new FormData();
    let reader = new FileReader();
    let file = e.target.files[0];

    if (e.target.files && e.target.files[0]) {
      reader.readAsDataURL(file);
      if (file == null) {
        this.toastService.toastMsg({
          title: "Error",
          content: "Please Select Image To Upload.",
        });
        // this.toastService.openErrorSnackBar(
        //   "Please Select Image To Upload.",
        // );
      } else {
        formdata.set("upload", file)
        this.productService.PostImagePdf(formdata).then((res) => {
          this.productPicUrl = res.data.location;
          console.log("this.productPicUrl", this.productPicUrl);

        });
      }
    }
  }
  cancel() {
    this.dialogRef.close();
  }
}
