import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { EditExpencemployeeComponent } from '../edit-expencemployee/edit-expencemployee.component';
import { AddExpencemployeeComponent } from '../add-expencemployee/add-expencemployee.component';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { EmpexpenseserviceService } from 'src/app/core/services/empexpenseservice.service';
import FileSaver from "file-saver";
import { saveAs } from "file-saver/dist/FileSaver";

@Component({
  selector: 'app-emp-expence',
  templateUrl: './emp-expence.component.html',
  styleUrls: ['./emp-expence.component.scss']
})
export class EmpExpenceComponent implements OnInit {

  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public userId:any;
  expenseAllData: any;
  expenseId: any;
  DataLink: any;

  constructor(public dialog: MatDialog,public rolesPermissionsService: RolesPermissionsService,
    private titleCasePipe: TitleCasePipe, private toastService: ToastNotificationService,
    public expenseservice : EmpexpenseserviceService ) { }

  ngOnInit(): void {
    console.log("employee ",this.currentEmployee,"employee Id",this.currentEmployee.id)

    this.getEmployeeByEmployeeId();
    this.getexpenseDetails();
    this.expenseservice.productAdded$.subscribe(() => {
      // Fetch the updated list of users
      this.getexpenseDetails();
    });
  }
  getEmployeeByEmployeeId(){
    this.rolesPermissionsService.getEmployeeById({}, this.currentEmployee.id).then((res) => {

      console.log("In Leave --> employee BY UDER Id",res.data);
      this.userId=res.data.parentId;
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");      
      }
    })

  }

  getexpenseDetails(){
    this.expenseservice.getAllExpenseDetails({},this.currentEmployee.id).then((res)=>{
      console.log("res Expense LIST",res);
      this.expenseAllData = res.data;
      
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");      
      }
    })
  }

  openAddexpense(userId){
    const dialogRef = this.dialog.open(AddExpencemployeeComponent, {
      width: '700px',
      height : '730px',
      data: {userId:userId},
      panelClass: 'custom-dialog-addexpence-class',
      disableClose:true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {

    });

  }

  openEditexpense(expenseId,userId) {
    const dialogRef = this.dialog.open(EditExpencemployeeComponent, {
      width: '700px',
      height : '730px',
      data: {expenseId:expenseId,userId:userId},
      panelClass: 'custom-dialog-updateexpense-class',
      disableClose:true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {

    });
  }

  getexpenseId(expenseId){
    this.expenseId=expenseId

  }
  deletExpenseById(){
    this.expenseservice.deleteExpense({},this.expenseId).then(res=>{
      // this.toastService.openSnackBar(" Expense Deleted Successfully")
      this.toastService.toastMsg({
        title: "Success",
        content: "Expense Deleted Successfully!!!",
      });
      this.getexpenseDetails();

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");      
      }
    })

  }

  GotoImageOrPDF(imagelink){
    this.DataLink=imagelink
    const pdfName = "document.pdf";
    FileSaver.saveAs(this.DataLink, pdfName);


  }

}
