import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainExpencemployeeComponent } from './main-expencemployee/main-expencemployee.component';
import { EmpExpenceComponent } from './emp-expence/emp-expence.component';

const routes: Routes = [
  {
    path:'',
    component:MainExpencemployeeComponent,
    children: [
      {
        path:'',
        redirectTo: 'emp-expence',
        pathMatch: 'full'
      },
      {
        path:'emp-expence',
        component:EmpExpenceComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeexpenceRoutingModule { }
